import React from "react"
import { Link } from "gatsby"
import './technology-facts.css'

import Layout from "../components/layout"
import SEO from "../components/seo"

import Pyramids    from '../images/pyramids/pyramids_image.png'
import Sun     from '../images/sun-disappers/sun_image.png'
import Moon    from '../images/moon-disappears/moon_image.png'
import Cicada         from '../images/cicada-3301/cicada_logo1.png'
import Area51      from '../images/area-51/area52_logo.png'
import Timetravel from '../images/time-travel/timetravel_logo.jpg'
import FlatEarth  from "../images/flat-earth/flate_dp.png"
import DevilSea        from '../images/devil-sea/devil_mysterios.png'

const SecondPage = () => (
  <Layout>
    <SEO title="Mystery Facts" />
    <h1>In this page you will get the articles related to the most unsolved mysteries and some amazing facts about the world</h1>
     
<div className="container">
    <div className="blog-card">
        <div className="meta">
            <div className="photo" >
            <img src={Pyramids} alt="the ancient pyramids" />
            </div>
            <ul className="details">
            <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
            <li className="date">Sept. 12, 2019</li>
                
            </ul>
        </div>
        <div className="description">
            <h1>Aliens and the pyramids </h1>
            <h2>  Aliens involvement in construction of pyramids in Egypt</h2>
            <p>Hope all of you might have listen and known about pyramids well its time to find some awesome and amazing facts about the great pyramids and its construction</p>
            <p className="read-more">
            <Link to="/mystery/pyramids-mystery/aliens-involvement-in-construction-of-pyramids">Read More</Link>
            </p>
        </div>
    </div>
  <div className="blog-card alt">
    <div className="meta">
      <div className="photo"     >
      <img src={Moon} alt="The moon orbitting the earth"  />

      </div>
      <ul className="details">
        <li className="author"><Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Sept. 15, 2019</li>
        
      </ul>
    </div>
    <div className="description">
      <h1>Moon facts </h1>
      <h2>What if the moon suddenly gets disappeared.</h2>
      <p> We often take the Moon for granted and never even give a thought to how our life would be without our little neighbour. Have you ever wondered what would happen if the moon suddenly disappeared?</p>
      <p className="read-more">
        <Link to="/mystery/moon-facts/what-if-moon-disappears">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card">
    <div className="meta">
      <div className="photo"  >
      <img src={Sun} alt="Sun rays reaching the earth"    />
      </div>
      <ul className="details">
      <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Aug. 24, 2019</li>
         
      </ul>
    </div>
    <div className="description">
      <h1>Sun facts    </h1>
      <h2>What if the sun suddenly gets disappeared.</h2>
      <p>We all know that the solar system exists because of sun and the sun keeps all the planets including the earth in its track.The sun is the lifeline of the solar system.Lets us dive deep and check what will happen if the sun gets disappeared or is removed from the solar system.</p>
      <p className="read-more">
        <Link to ="/mystery/sun-facts/what-if-the-sun-disappears">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card alt">
    <div className="meta">
        <div className="photo"    >
        <img src={DevilSea} alt="Devil Sea swallowing ships and planes"     />
            </div>
      <ul className="details">
        <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Aug. 20, 2019</li>
           
      </ul>
    </div>
    <div className="description">
      <h1>Devil Sea </h1>
      <h2>The mystery of the Devil's Sea </h2>
      <p> Hope all of you have heard about the incidents at the bermuda triangle but there is another place on this earth where similar incidents happens.Have you ever heard about 'The Devil's Triangle' located in the Japan sea which have claimed more lives than the Bermuda triangle.</p>
      <p className="read-more">
       <Link to ="/mystery/devil-sea/the-mystery-of-devil-sea">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card">
    <div className="meta">
        <div className="photo"    >
        <img src={Cicada} alt="The biggest mystery in the internet"   />
            </div>
      <ul className="details">
        <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Aug. 15, 2019</li>
         
      </ul>
    </div>
    <div className="description">
      <h1>  Cicada 3301  </h1>
      <h2> The biggest unsolved mystery on internet Cicada 3301.  </h2>
      <p>Hope everyone loves to solve puzzles. Let's explore the biggest puzzles of the internet which is still unsolved.  </p>
      <p className="read-more">
        <Link to ="/mystery/cicada-3301/the-unsolved-mystery-of-cicada-3301">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card alt">
    <div className="meta">
        <div className="photo"  >
        <img src={Area51} alt="the area 51 where aliens interact with humans" style={{marginTop:`40%`}}/>
            <img src={Area51} alt="the area 51 where aliens interact with humans" style={{marginTop:`40%`}}/>
        </div>
      <ul className="details">
      <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Aug. 10, 2019</li>
           
      </ul>
    </div>
    <div className="description">
      <h1>Area 51  </h1>
      <h2> The secret behind the mysterious area 51. </h2>
      <p>Hope you know about the mysterious place of area 51 where aliens visit the earth and exchanges it technology with the American Air Force.</p>
      <p className="read-more">
        <Link to="/mystery/area-51/the-mystery-of-area-51-revealed">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card">
    <div className="meta">
        <div className="photo"    >
            <img src={Timetravel} alt="Time travel incidents "    />
            </div>
      <ul className="details">
      <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Sept. 15, 2019</li>
         
      </ul>
    </div>
    <div className="description">
      <h1>  Time travel   </h1>
      <h2>  The mystery of time travel. </h2>
      <p> Time travel had always been a mystery for everyone. There are many instances in which time travel incidents have been proved.</p>
        <p className="read-more">
        <Link to ="/mystery/time-travel/the-mystery-of-time-travel-revealed ">Read More</Link>
      </p>
    </div>
  </div>
  <div className="blog-card">
    <div className="meta">
        <div className="photo"    >
            <img src={FlatEarth} alt="Flat earth theory claiming the earth is flat"    />
            </div>
      <ul className="details">
        <li className="author"> <Link to="/about-me"> Sagar Giri </Link></li>
        <li className="date">Sep. 24, 2015</li>
       
      </ul>
    </div>
    <div className="description">
      <h1>Flat earth </h1>
      <h2>  The reality of flat earth society.   </h2>
      <p> ou all know that the earth is round but there are many people in this world called as the flat earth society who believes thar the earth is not round rather it is flat.Various proofs have been put forward to prove it.  </p>
      <p className="read-more">
        <Link to="/mystery/flat-earth/the-reality-of-flat-earth-society-debunked">Read More</Link>
      </p>
    </div>
  </div>
 
</div>

  </Layout>
)

export default SecondPage
